import React,{Component} from "react";
import ContactPersonTemplate from "./ContactPersonTemplate";
import axios from 'axios'
import {URL} from '../../../config'
import ReactLoading from 'react-loading'

class ContactPerson extends Component {
  constructor(props){
    super(props)
  this.state = {
    contacts :[],
    isLoaded:false,
    error:null
  }
}

Loading = ({ type, color }) => (
  <ReactLoading type={type} color={color} height={300} width={200} />
);

  componentDidMount(){
    const id= this.props.id
    axios.get(`${URL}/api/index/company/contacts/${id}`)
    .then(res => res.data)
    .then((res)=>{
      this.setState({
        isLoaded:true,
        contacts : res
      })
  },
  (error)=>{
    this.setState({
      isLoaded:true,
      error
    })
  })
  }
  deleteContact = (id) =>{
    //using this id i will delete contact
    axios.delete(`${URL}/api/index/company/contact/${id}`).then((res)=>{
      
      const filterContact = this.state.contacts.filter((item)=>{
        return item.id !== id
      })
      this.setState({
        contacts : filterContact
      })
      this.ContactList(this.state.contacts)
  })
}
  ContactList = (data)=>{
    const Template = data.map((item,i)=>(
 
        <ContactPersonTemplate
        className="item" 
          key={i}
          number={i}
          CPid={item.id}
          CPName={item.name}
          CPEmail={item.email}
          CPAddress={item.address}
          CPPhoneNumber={item.phoneNumber}
          CPPosition={item.position}
          deleteContact={this.deleteContact}
        />
    ))
    return Template
  }
  render(){
    const {error, isLoaded} = this.state;
    if(error){
      return <div>Error: {error.message}</div>;
    }
    else if (!isLoaded) {
      return <div className="offset-4">{this.Loading({type:'spin',color:'#ffdefd'})}</div>;
    }
    else{
  return (
    <div className="container">
    <div className="row">
        <div className="col-md-12">
            <div className="">
                <div className="panel-heading text-center">
                    <span className="title text-secondary text-center text-bolder">Company Contact Persons</span>
                </div>
        {this.ContactList(this.state.contacts)}
        <br className="mb-3" />
        </div>
            </div>
        </div>
	</div>
      
  );
  }
}
};

export default ContactPerson;
