import React, { Component } from "react";
import axios from 'axios'
import {URL} from '../../../config.js'
import Alert from '../../widget/alert'

class ContactTemplate extends Component {
  
    state = {
    fullName: this.props.contact.name,
    phoneNumber: this.props.contact.phoneNumber,
    email: this.props.contact.email,
    position: this.props.contact.position,
    Cid: this.props.contact.Cid,
    address : this.props.contact.address,
    id:this.props.contact.id,
    message : ''
  };
  //company id from /:id params
  fullNameHandler = (event) => {
    this.setState({
      fullName: event.target.value
    });
  };
  phoneNumberHandler = (event) => {
    this.setState({
      phoneNumber: event.target.value
    });
  };
  emailHandler = (event) => {
    this.setState({
      email: event.target.value
    });
  };
  positionHandler = (event) => {
    this.setState({
      position: event.target.value
    });
  };
  addressHandler = (event) => {
    this.setState({
      address: event.target.value
    });
  };
  AddContact = (event) => {
    event.preventDefault();
    //add new contact
    const data = {
      name: this.state.fullName,
      phoneNumber: this.state.phoneNumber,
      email: this.state.email,
      position: this.state.position,
      address: this.state.address,
      Cid:this.state.Cid
    };
    const Cid = this.state.id
    axios.put(`${URL}/api/index/company/contact/${Cid}`,data).then((res)=>{
      if(res.data){
          setTimeout(() => {
            this.setState({
              message:"",
            })
          }, 1000);
          this.setState({
            message:"Contact Updated",
          })
      } else{
        this.setState({
          message : "unable to connect to the server"
        })
      }
    })
  };

  render() {
    return (
      <div className="col-md-6 mx-auto text-left">
        <form onSubmit={this.AddContact}>
          <h3 className="text-center">Update Contact Person</h3>
          <div className="input-group mb-3">
                  {this.state.message === "" ? (
                    <span></span>
                  ) : (
                    <Alert message={this.state.message} />
                  )}
                </div>
          <div className="form-group">
            <label>Full Name</label>
            <input
              type="text"
              className="form-control"
              value={this.state.fullName}
              onChange={this.fullNameHandler}
              placeholder="full Name"
            />
          </div>

          <div className="form-group">
            <label>Phone Number</label>
            <input
              type="text"
              className="form-control"
              value={this.state.phoneNumber}
              onChange={this.phoneNumberHandler}
              placeholder="Phone Number"
            />
          </div>

          <div className="form-group">
            <label>Email address</label>
            <input
              type="email"
              className="form-control"
              value={this.state.email}
              onChange={this.emailHandler}
              placeholder="Enter email"
            />
          </div>

          <div className="form-group">
            <label>Contact Person Position</label>
            <input
              type="text"
              className="form-control"
              value={this.state.position}
              onChange={this.positionHandler}
              placeholder="contact person position"
            />
          </div>
          <div className="form-group">
            <label>Contact Person Address</label>
            <input
              type="text"
              className="form-control"
              value={this.state.address}
              onChange={this.addressHandler}
              placeholder="contact person address"
            />
          </div>
          <button type="submit" className="btn btn-primary btn-block">
            Update Contact
          </button>
        </form>
      </div>
    );
  }
}

export default ContactTemplate;
