import React from 'react'
import {Link} from 'react-router-dom'
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css";

const Row = (props)=>{
    const item = props.data
    const submit = (id) => {
      confirmAlert({
        title: "Delete Item",
        message: "Are you sure want to delete this item?",
        buttons: [
          {
            label: "Yes",
            onClick: () => deleteModal(id)
            
          },
          {
            label: "No",
          }
        ]
      });
    };
    const deleteModal=(id)=>{
      props.deleteCompany(item.Cid)
    }
   return (
        <tr key={item.Cid}>
          <td>{item.Cname.slice(0,15)}</td>
          <td>{item.Ccategory}</td>
          <td>{item.Cemail.slice(0,15)}</td>
          <td>{item.CphoneNumber.slice(0,15)}</td>
          <td>
            <Link to={{
                pathname:`/company/${item.Cid}`
            }}>more info</Link>
          </td>
          <td><button onClick={()=>{submit(item.Cid)}} className="btn btn-danger"><i className="fa fa-trash" aria-hidden="true"></i></button></td>
       
        </tr>
   )
}
