import React from "react";

const Alert = (props) => {
  return (
    <div className="alert alert-danger alert-dismissible fade show col-md-12" role="alert">
      {props.message}
      <button
        type="button"
        className="btn-close float-right btn-danger"
        data-bs-dismiss="alert"
        aria-label="Close"
      >x</button>
    </div>
  );
};

export default Alert;
