import React from "react";
import {Link} from "react-router-dom"

const CompanyTemplate = (props) => {
  return (
    <section className="jumbotron">
      <div className="container">
        <h1 className="jumbotron-heading text-center">
          <div className="bg-dark text-light" style={{position:'relative'}}>
            {props.company.Cimage ? (
              <img alt="" src={props.company.Cimage} className="img-fluid" />
            ) : (
              `${props.company.Cname}`
            )}
              <Link
              className="nav-link font-weight-bolder text-light listEditI"
              role="button"
              to = {{
                  pathname:`/company/edit/${props.Id}`
              }}
              company={props.company}
            >
                <button className="btn-warning btn-sm"><i className="fa fa-paint-brush fa-sm" aria-hidden="true"></i></button>
            </Link>
          </div>
          <div className="mx-auto col-md-6">
            {props.company.Cimage? (
            <img
              alt={`${props.company.Cname}`}
              src={props.company.Cimage}
              className="img-fluid img-thumbnail"
            />) : (
              <span></span>
            )}
          </div>
        </h1>
        <div className="mx-3">
        <p className="lead text-muted">{props.company.Cdescription}</p>
        <p className="lead text-muted"><i className="fa fa-phone" aria-hidden="true"></i> {props.company.CphoneNumber}</p>
        <p className="lead text-muted"><i className="fas fa-mail-bulk    "></i> <a href={`mailto:${props.company.Cemail}`}>{props.company.Cemail}</a> </p>
        <p className="lead text-muted"><i className="fa fa-object-group" aria-hidden="true"></i> {props.company.Ccategory}</p>
        <p>
          <a href={`https://www.${props.company.Cwebsite}`} className="btn btn-primary my-2">
           <i className="fa fa-globe" aria-hidden="true"></i> {props.company.Cwebsite}
          </a>
        </p>
        </div>
      </div>
    </section>
  );
};

export default CompanyTemplate;
