import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import Welcome from "./components/welcome/welcome";
import Home from "./components/Home/home"
import Company from "./components/Company/company";
import NotFound from "./components/404/404.js"
import AddCompany from "./components/Dashboard/AddCompany/addCompany"
import Contact from "./components/Company/ContactPerson/contact"
import EditCompany from "./components/Company/editCompany"

class Routes extends Component {
  render() {
    return (
        <Switch>
            <Route path="/" exact component={Welcome}/>
            <Route path="/home" exact component={Home}/>
            <Route path="/company/:id" exact component={Company}/>
            <Route path="/add" exact component={AddCompany}/>
            <Route path="/company/contact/:id" exact component={Contact}/>
            <Route path="/company/edit/:id" exact component={EditCompany}/>
            <Route component={NotFound}/>
        </Switch>
    );
  }
}

export default Routes;