import React from "react";

const Sidebar = (props) => {
  return (
    <div className=" offset-1 col-md-2 mx-auto mt-2">
        <div className="card" style={{ width: "18rem" }}>
          <div className="card-header bg-dark text-light">Company Categories</div>
          <ul className="list-group list-group-flush  mt-3">
            <li className="list-group-item">
               <button onClick={()=>props.handleCategory('')} className={props.category==='' ?'btn btn-secondary btn-block' : 'btn btn-primary btn-block'}>All</button>
            </li>
            <li className="list-group-item">
              <button onClick={()=>props.handleCategory('Governmetal')} className={props.category==='Governmetal' ?'btn btn-secondary btn-block' : 'btn btn-primary btn-block'}>Governmental</button>
            </li>
            <li className="list-group-item ">
              <button onClick={()=>props.handleCategory('NGOs')} className={props.category==='NGOs' ?'btn btn-secondary btn-block' : 'btn btn-primary btn-block'}>NGO's</button>
            </li>
            <li className="list-group-item  ">
              <button onClick={()=>props.handleCategory('Commercial')} className={props.category==='Commercial' ?'btn btn-secondary btn-block' : 'btn btn-primary btn-block'}>Commercials</button>
            </li>
            <li className="list-group-item ">
              <button onClick={()=>props.handleCategory('Residential')} className={props.category==='Residential' ?'btn btn-secondary btn-block' : 'btn btn-primary btn-block'}>Residentials</button>
            </li>
            
            <li className="list-group-item ">
              <button onClick={()=>props.handleCategory('Supplier')} className={props.category==='Others' ?'btn btn-secondary btn-block' : 'btn btn-primary btn-block'}>Supplier</button>
            </li>
            <li className="list-group-item ">
              <button onClick={()=>props.handleCategory('Others')} className={props.category==='Others' ?'btn btn-secondary btn-block' : 'btn btn-primary btn-block'}>Others</button>
            </li>
          </ul>
      </div>
    </div>
  );
};

export default Sidebar;
