import React, { Component } from "react";
import Header from "../../Header/header";
import axios from 'axios'
import {URL} from '../../../config'
import ReactLoading from 'react-loading'
import ContactTemplate from "./contactTemplate";
import { Link } from "react-router-dom";

class Contact extends Component {
  constructor(props){
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      contact:{},
      toggle:true,
      CompanyId:this.props.match.params.id
    }
  }
  componentDidMount() {
    const id= this.props.match.params.id
    axios.get(`${URL}/api/index/contact/${id}`)
      .then(res => res.data)
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            contact: result[0]
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }
  Loading = ({ type, color }) => (
    <ReactLoading type={type} color={color} height={300} width={200} />
  );
  cursor = {
    cursor: "pointer"
  };
  toggleAdd = ()=>{
    this.setState({
       toggle:false
    })
  }
  toggleC = ()=>{
    this.setState({
      toggle:true
    })
  }
  render() {
    const {error, isLoaded} = this.state
    if(error){
      return <div>Error: {error.message}</div>;
    }
    else if (!isLoaded) {
      return <div className="offset-4">{this.Loading({type:'spin',color:'#ffdefd'})}</div>;
    }
    else{
    return (
      <div className="containers">
      <Header />
      <article className="container-fluid mt-2">
        <ul className="breadcrumb pl-5 mb-1 ">
          <li className="breadcrumb-item"  style={{color:'blue'}} 
          >
          <Link to={{
            pathname:`/company/${this.state.contact.Cid}`
        }}><i className="fa fa-backward" aria-hidden="true"></i> Company</Link>
        </li>
        </ul>
        <div className="mt-2">
        <ContactTemplate contact={this.state.contact}/>
        </div>
        </article>
        </div>
    );
  }
}
}

export default Contact;
