import React, { Component } from "react";
import Alert from '../../widget/alert'
import axios from 'axios'
import { URL } from "../../../config";
import Header from "../../Header/header"

class AddCompany extends Component {
  //i will do image upload ... on other version

  state = {
    options:["NGOs","Governmetal","Commercial","Residential","Supplier","Others"],
    selectedOption:"NGOs",
    companyName: "",
    companyDescription: "",
    companyCategory: "",
    companyPhoneNumber: "",
    companyWebsite: "",
    message:"",
    companyEmail:""
  }
  handleChange = e =>{
    this.setState({
      [e.target.name]: e.target.value
    })
    console.log(this.state.selectedOption)
  }
  handleCPName =( e) =>{
    this.setState({
      companyName: e.target.value
    })
  }
  handleCPDescription = e =>{
    this.setState({
      companyDescription: e.target.value
    })
  }
  handleCPWebsite = e =>{
    this.setState({
      companyWebsite: e.target.value
    })
  }
  handleCPPhoneNumber = e =>{
    this.setState({
      companyPhoneNumber: e.target.value
    })
  }
  handleCPEmail = e =>{
    this.setState({
      companyEmail: e.target.value
    })
  }
  AddCustomer = (e) => {
    e.preventDefault();
    const customer = {
      companyName: this.state.companyName,
      companyEmail:this.state.companyEmail,
      companyDescription: this.state.companyDescription,
      companyPhoneNumber: this.state.companyPhoneNumber,
      companyCategory:this.state.selectedOption,
      companyWebsite: this.state.companyWebsite  
    };
    if(this.state.companyName === ''){
      this.setState({message : "Please Fill out the form"})
    }
    else{
      setTimeout(() => {
        this.setState({message:"",companyName: "",
        companyDescription: "",
        companyCategory: "",
        companyPhoneNumber: "",
        companyWebsite: "",
        companyEmail:""})
      }, 1000);
      this.setState({message : "Company Customer Data Have been Added"})

      axios.post(`${URL}/api/index/company`,customer).then((res)=>{

      })
    }
    };
  render() {
    return (
      <div className="container-fluid">
        <Header/>
        <h2 className="my-2 text-center"> Add Company </h2>
        <form
          onSubmit={this.AddCustomer}
          className="text-left col-md-6 mx-auto"
        >
          <div className="input-group mb-2">
            {this.state.message === "" ? (
              <span></span>
            ) : (
              <Alert message={this.state.message} />
            )}
          </div>
          <div className="form-group">
            <label>Company Name</label>
            <input
              name="companyName"
              type="text"
              className="form-control"
              placeholder="company name here.."
              value={this.state.companyName}
              onChange={this.handleCPName}
              required
            />
          </div>
          <div className="form-group">
            <label >Company Description</label>
            <textarea
              name="companyDescription"
              className="form-control"
              col="40"
              row="10"
              value={this.state.companyDescription}
              onChange={this.handleCPDescription}
            ></textarea>
          </div>
          <div className="form-group">
            <label>Company Website</label>
            <input
              name="companyWebsite"
              type="text"
              className="form-control"
              placeholder="company Website here.."
              value={this.state.companyWebsite}
              onChange={this.handleCPWebsite}
            />
          </div>
          <div className="form-group">
            <label >Company Phonenumber</label>
            <input
              name="companyPhoneNumber"
              type="text"
              className="form-control"
              placeholder="company Phonenumber here.."
              value={this.state.companyPhoneNumber}
              onChange={this.handleCPPhoneNumber}
            />
          </div>
          <div className="form-group">
            <label >Company Email</label>
            <input
              name="companyPhoneNumber"
              type="email"
              className="form-control"
              placeholder="company Phonenumber here.."
              value={this.state.companyEmail}
              onChange={this.handleCPEmail}
            />
          </div>
          <div className="form-group">
            <label >Company Category </label>
            <select name='selectedOption' className="form-control" onChange={this.handleChange}>
            {this.state.options.map(i => i === this.state.selectedOption ? (
            <option key={i} value={i} >
              {i}
            </option>
            ) : (<option key={i} value={i}>{i}</option>) )}
          </select>
          </div>
          <button className="btn btn-secondary btn-block" type="submit">
            submit
          </button>
        </form>
      </div>
    );
  }
}

export default AddCompany;
