import React, { Component } from "react";
import CompanyTemplate from "./companyTemplate";
import Header from "../Header/header";
import axios from 'axios'
import {URL} from '../../config'
import AddContactPerson from "../Dashboard/ContactPerson/AddContactPerson/addContactPerson";
import ContactPerson from "./ContactPerson/contactPerson";
import ReactLoading from 'react-loading'
import {Link} from 'react-router-dom'

class Company extends Component {

  constructor(props){
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      company:[],
      toggle:true,
      CompanyId:this.props.match.params.id
    }
  }
  componentDidMount() {
    const id= this.props.match.params.id
    axios.get(`${URL}/api/index/company/${id}`)
      .then(res => res.data)
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            company: result[0]
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }
  Loading = ({ type, color }) => (
    <ReactLoading type={type} color={color} height={300} width={200} />
  );
  cursor = {
    cursor: "pointer"
  };
  toggleAdd = ()=>{
    this.setState({
       toggle:false
    })
  }
  toggleC = ()=>{
    this.setState({
      toggle:true
    })
  }
  render() {
    const {error, isLoaded} = this.state
    if(error){
      return <div>Error: {error.message}</div>;
    }
    else if (!isLoaded) {
      return <div className="offset-4">{this.Loading({type:'spin',color:'#ffdefd'})}</div>;
    }
    else{
    return (
      <div className="containers">
      <Header />
      <article className="container-fluid mt-2">
        <ul className="breadcrumb pl-5 mb-1 ">
        <li className="breadcrumb-item"  style={{color:'blue'}} 
        >
        <Link to={{
          pathname:`/home`
      }}><i className="fa fa-backward" aria-hidden="true"></i></Link>
      </li>
          <li className="breadcrumb-item"  style={{color:'blue'}} onClick={this.toggleC}>
            Company
          </li>
          <li className="breadcrumb-item" style={this.cursor} onClick={this.toggleAdd}>
            Add Contact Person
          </li>
        </ul>
      <div className="mt-2">
      {this.state.toggle? [
        <span>
        <CompanyTemplate
          company={this.state.company}
          Id={this.state.CompanyId}
        />
        <ContactPerson key={this.state.CompanyId} id={this.state.CompanyId}/>
        </span>] : (
          <AddContactPerson companyId={this.state.CompanyId}/>
        )}
        </div>
        </article>
        </div>
    );
  }
}
}

export default Company;
