import React from "react";
import './contact.css'
import { Link } from "react-router-dom";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css";


const ContactPersonTemplate = (props) => {

    const submit = (id) => {
        confirmAlert({
          title: "Delete Contact",
          message: "Are you sure want to delete this contact person?",
          buttons: [
            {
              label: "Yes",
              onClick: () => deleteModal(id)
              
            },
            {
              label: "No",
            }
          ]
        });
      };
      
    const deleteModal=(id)=>{
        props.deleteContact(id)
      }

  return (
      <ul className="list-group mt-2" id="contact-list">
          <li className="list-group-item">
              <div className="col-md-3 float-left">
              <button className="btn">
              <span className="badge badge-secondary">{props.number+1}</span>
          </button>
                  <img src="/contact.png" style={{maxHeight:'100px'}} alt={props.CPName} className="rounded" />
              </div>
              <span className="text-primary lead text-bolder">{props.CPName}</span>
              <div className="col-md-6 float-right listMain" style={{
                fontFamily: 'Courier New'}}>
                  <span> <i className="fa fa-phone text-primary" aria-hidden="true"></i> {props.CPPhoneNumber}</span><br/>
                  <span> <i className="fas fa-mail-bulk  text-primary  "></i> {props.CPEmail}</span><br/>
                  <span> <i className="fa fa-id-badge text-primary" aria-hidden="true"></i> {props.CPPosition}</span><br/>
                  <span> <i className="fa fa-location-arrow text-primary" aria-hidden="true"></i> {props.CPAddress}</span><br/> 
              </div>
              <div className="listEdit">
              <Link
                className="nav-link font-weight-bolder text-light"
                role="button"
                to = {{
                    pathname:`/company/contact/${props.CPid}`
                }}
              >
                  <button className="btn-warning"><i className="fa fa-paint-brush" aria-hidden="true"></i></button>
              </Link>
          </div>
              <div className="listDelete">
                  <button className="btn-danger" onClick={()=>submit(props.CPid)} ><i className="fa fa-trash" aria-hidden="true"></i></button>
              </div>

              </li>
          </ul>
  );
};

export default ContactPersonTemplate;