import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import './welcome.css'

export default class Welcome extends Component {
    
    render() {
        return (
            <div
      style={{
        backgroundImage: `url('/bg.jpeg')`,
        width:"100%",
        height:"900px"
      }}
    >
            <div className="container-fluid " >
                <div className="welcome-center mx-auto">
                    <h2 className="my-2 text-center">Welcome <br/> Store All Your Customer Data In One Place</h2>
                    <Link to="/home" className="w-btn btn btn-primary btn-lg text-bolder">Continue</Link>
                </div>
            </div>
            </div>
        )
    }
}