import React from "react";
import Sidebar from "../../hoc/Sidebar/sidebar";
import Filter from "../widget/filter";
import Table from "../widget/table";
import Header from "../Header/header"
import axios from "axios";
import { URL } from "../../config";
import Row from "../widget/row";


class Home extends React.Component {
  // s
  constructor(props){
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items:[],
      search:'',
      category:''
    }
  }
// table

componentDidMount() {
  axios.get(`${URL}/api/index/company`)
    .then(res => res.data)
    .then(
      (result) => {
        this.setState({
          isLoaded: true,
          items: result
        });
      },
      // Note: it's important to handle errors here
      // instead of a catch() block so that we don't swallow
      // exceptions from actual bugs in components.
      (error) => {
        this.setState({
          isLoaded: true,
          error
        });
      }
    )
}

//this function will delete a contact
deleteCompany = (id)=>{
//using 'id' delete comp
axios.delete(`${URL}/api/index/company/${id}`).then((res)=>{
  const filterCompany = this.state.items.filter((item)=>{
    return item.Cid !== id
  })
  
this.setState({
  items: filterCompany
})
this.filterData(this.state.items)
})
}
filterData(profile,search='',category=''){
  // return(this.state.items(item=>{
  //   return this.state.item.Cname.toLowerCase().match(search.toLowerCase())
  // }))
  let data = []
  let template = []
  if(category!==''){
    const Cprofile = profile.filter((item)=>{
      return (item.Ccategory===category)
    })
     data = Cprofile.filter((item,i)=>{
      return (item.Cname.toLowerCase().indexOf(search.toLowerCase())===0)
    })
    template = data.map((item,i)=>{
      return(
      <Row className="item" key={i} data={item} deleteCompany = {this.deleteCompany}  />)
    })
    return template
  }
  else{
   data = profile.filter((item,i)=>{
    return (item.Cname.toLowerCase().indexOf(search.toLowerCase())===0)
  })
  template = data.map((item,i)=>{
    return(
    <Row className="item" key={i} data={item} deleteCompany = {this.deleteCompany}  />)
  })
  // this.renderTableBody(data)
  return template
  }

}
handleSearch = e =>{
  this.setState({
    search: e.target.value
  })
  this.filterData(this.state.items,e.target.value)
}
handleCategory = (categorys)=>{
  this.setState({
    category : categorys,
  })
  this.filterData(this.state.items,'',this.state.category)
}
// 




  render(){
  return (
    <div className="home-container">
      <Header/>
      <Filter handleSearch={this.handleSearch}  search={this.state.search}/>
      <div className="row">
        <div className="col-md-8">
          <Table stuff={this.state} handleSearch={this.handleSearch} filterData={this.filterData} deleteCompany={this.deleteCompany} />
        </div>
        <Sidebar handleCategory={this.handleCategory} category={this.state.category}/>
      </div>
    </div>
  );
  }
};

export default Home;